
.main {
    /* text-align: center;s */
    font-family: 'Patrick Hand', cursive;
    font-size: 18px;
    padding-right: 20px;
}
.body {
    display: flex;
    /* position: relative; */
}
.main .header {
    text-align: center;
    font-family: 'Poppins';
}
button {
    margin-left: 20px;
    padding: 8px 12px;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Patrick Hand', cursive;
    background-color: #ddd;
    border: none;
    border-radius: 4px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    cursor: pointer;
    
}

button:hover {
    background-color: #ccc;
}

.work button {
    font-size: 12px;
    width: 30%;
    height: 30px;
}
.work.dragging {
	pointer-events: none;
	position: fixed;
    z-index: 1000;
    font-family: 'Patrick Hand', cursive;
}
.listwork.dragging {
    pointer-events: none;
	position: fixed;
    z-index: 1000;
    font-family: 'Patrick Hand', cursive;
}
.input {
    margin-left: 12px;
    width: 20.5%;
    height: 28px;
    margin-top: 5px;
    text-indent: 5px;
    font-size: 18px;
    border-radius: 5px;
    border: solid 1.5px #D3D3D3;
    -webkit-transition: 1s; /* Safari */
    transition: 1s;
    margin-bottom: 30px;
}
.input[type=text]:hover{
box-shadow: 0 0 5pt 0.5pt #D3D3D3;
}
.input[type=text]:focus {
box-shadow: 0 0 5pt 2pt #D3D3D3;
outline-width: 0px;
}

.work {
    display: flex;
    background: white;
    margin: 5px;
    border-radius: 4px;
    padding: 10px;
    border: 1px solid #ddd;
    pointer-events: auto;
    cursor: pointer;
    z-index: 1;
    user-select: none;
    font-size: 16px;
}

.work:hover{
    background: #fafafa;
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000px;
}
 
@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  
  /* 20%, 80% {
    transform: translate3d(2px, 0, 0);
  } */
 
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
 
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
.work:active{
    background: #92A9BD;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

